<template>
  <v-app>
    <!-- Floating Toggle Button for Restrictions -->
    <div class="floating-toggle">
      <v-menu offset-y>
        <template #activator="{ props }">
          <v-btn
            icon="mdi-shield-lock"
            variant="flat"
            color="primary"
            size="large"
            v-bind="props"
            aria-label="Abrir controles de seguridad y notificaciones"
          />
        </template>

        <v-list density="compact" min-width="280">
          <v-list-item title="Controles de Seguridad" subtitle="Protecci√≥n y notificaciones" />

          <v-divider class="my-2" />

          <v-list-item
            :title="`Bloquear Clic Derecho: ${isBlockRightClickEnabled ? 'Activado' : 'Desactivado'}`"
            @click="toggleBlockRightClick"
          >
            <template #prepend>
              <v-switch
                :model-value="isBlockRightClickEnabled"
                @update:model-value="isBlockRightClickEnabled = $event"
                hide-details
              />
            </template>
          </v-list-item>

          <v-list-item
            :title="`Notificaciones: ${isNotificationsEnabled ? 'Activadas' : 'Desactivadas'}`"
            @click="toggleNotifications"
          >
            <template #prepend>
              <v-switch
                :model-value="isNotificationsEnabled"
                @update:model-value="isNotificationsEnabled = $event"
                hide-details
              />
            </template>
          </v-list-item>

          <v-divider class="my-2" />

          <v-list-item
            v-if="Notification?.permission === 'default'"
            title="Solicitar Permiso de Notificaciones"
            @click="requestNotificationPermission"
          >
            <template #prepend>
              <v-icon color="warning">mdi-bell-alert</v-icon>
            </template>
          </v-list-item>

          <v-list-item
            v-else
            :title="`Permiso: ${Notification?.permission || 'No soportado'}`"
            subtitle="Estado actual del navegador"
          >
            <template #prepend>
              <v-icon :color="Notification?.permission === 'granted' ? 'success' : 'error'">
                {{ Notification?.permission === 'granted' ? 'mdi-check-circle' : 'mdi-alert-circle' }}
              </v-icon>
            </template>
          </v-list-item>
        </v-list>
      </v-menu>
    </div>

    <v-main>
      <transition name="page-fade" mode="out-in">
        <router-view :key="$route.fullPath" class="page-transition" />
      </transition>
    </v-main>
  </v-app>
</template>

<script>
import { useRestrictions } from '@/composables/useRestrictions'

export default {
  name: 'App',

  setup() {
    const {
      isBlockRightClickEnabled,
      isNotificationsEnabled,
      requestNotificationPermission,
    } = useRestrictions()

    return {
      isBlockRightClickEnabled,
      isNotificationsEnabled,
      requestNotificationPermission,
      Notification: typeof window !== 'undefined' ? window.Notification : null,
    }
  },

  methods: {
    toggleBlockRightClick() {
      this.isBlockRightClickEnabled = !this.isBlockRightClickEnabled
    },

    toggleNotifications() {
      this.isNotificationsEnabled = !this.isNotificationsEnabled
    },
  },
}
</script>

