// composables/useRestrictions.js
import { ref, onMounted, onBeforeUnmount } from 'vue'

export function useRestrictions() {
  // State
  const isBlockRightClickEnabled = ref(true)
  const isNotificationsEnabled = ref(true)
  const originalTitle = ref('')
  const originalFavicon = ref('')
  const titleTimer = ref(null)
  const faviconTimer = ref(null)

  // Audio context for notification sound
  const audioContext = ref(null)

  /**
   * Initialize the audio context for sound notifications
   */
  const initAudioContext = () => {
    if (!audioContext.value && typeof window !== 'undefined') {
      try {
        const AudioContext = window.AudioContext || window.webkitAudioContext
        audioContext.value = new AudioContext()
      } catch (err) {
        // Audio not supported
      }
    }
  }

  /**
   * Play a simple notification beep sound
   */
  const playNotificationSound = () => {
    if (!audioContext.value) return

    try {
      const ctx = audioContext.value
      const now = ctx.currentTime

      // Create oscillator for beep
      const osc = ctx.createOscillator()
      const gain = ctx.createGain()

      osc.connect(gain)
      gain.connect(ctx.destination)

      // Set up sound parameters
      osc.frequency.setValueAtTime(800, now) // 800 Hz
      osc.frequency.setValueAtTime(1000, now + 0.1)
      gain.gain.setValueAtTime(0.3, now)
      gain.gain.exponentialRampToValueAtTime(0.01, now + 0.2)

      osc.start(now)
      osc.stop(now + 0.2)
    } catch (err) {
      // Silently fail if audio fails
    }
  }

  /**
   * Handle right-click blocking
   */
  const handleContextMenu = (e) => {
    if (isBlockRightClickEnabled.value) {
      e.preventDefault()
    }
  }

  /**
   * Handle visibility changes
   */
  const handleVisibilityChange = () => {
    if (document.hidden) {
      // Page is hidden
      if (isNotificationsEnabled.value) {
        showHiddenNotification()
        startTitleBlink()
        startFaviconBlink()
        playNotificationSound()
      }
    } else {
      // Page is visible again
      stopTitleBlink()
      stopFaviconBlink()
      if (originalTitle.value) document.title = originalTitle.value
      if (originalFavicon.value) {
        const link = document.querySelector("link[rel*='icon']")
        if (link) link.href = originalFavicon.value
      }
      try {
        window.focus()
      } catch (e) {
        /* ignore */
      }
    }
  }

  /**
   * Show notification when page is hidden
   */
  const showHiddenNotification = async () => {
    if (!('Notification' in window)) return

    if (Notification.permission === 'granted') {
      try {
        const opts = {
          body: '¬°La tienda sigue abierta! Haz click para volver.',
          icon: '/img/icons/icon-192.png',
          tag: 'store-notif',
          badge: '/img/icons/icon-96.png',
        }
        const n = new Notification('üîî Vuelve a Multi Tienda', opts)
        n.onclick = () => {
          try {
            window.focus()
          } catch (e) {
            /* ignore */
          }
          n.close?.()
        }
      } catch (err) {
        /* fallback to title blink only */
      }
    }
  }

  /**
   * Request notification permission
   */
  const requestNotificationPermission = async () => {
    if (!('Notification' in window)) return

    try {
      if (Notification.permission === 'default') {
        const permission = await Notification.requestPermission()
        return permission === 'granted'
      }
      return Notification.permission === 'granted'
    } catch (err) {
      return false
    }
  }

  /**
   * Start blinking the page title
   */
  const startTitleBlink = () => {
    if (titleTimer.value) return

    let visible = false
    const alt = 'üîî Vuelve a la tienda'

    titleTimer.value = setInterval(() => {
      document.title = visible ? originalTitle.value : alt
      visible = !visible
    }, 1000)
  }

  /**
   * Stop blinking the page title
   */
  const stopTitleBlink = () => {
    if (titleTimer.value) {
      clearInterval(titleTimer.value)
      titleTimer.value = null
    }
  }

  /**
   * Start blinking the favicon
   */
  const startFaviconBlink = () => {
    if (faviconTimer.value) return

    let visible = false
    const notifFavicon =
      'data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="50" cy="50" r="45" fill="%23ef4444"/><text x="50" y="55" font-size="60" fill="white" text-anchor="middle" dominant-baseline="middle">!</text></svg>'

    faviconTimer.value = setInterval(() => {
      const link = document.querySelector("link[rel*='icon']")
      if (link) {
        link.href = visible ? originalFavicon.value : notifFavicon
      }
      visible = !visible
    }, 1000)
  }

  /**
   * Stop blinking the favicon
   */
  const stopFaviconBlink = () => {
    if (faviconTimer.value) {
      clearInterval(faviconTimer.value)
      faviconTimer.value = null
    }
  }

  /**
   * Initialize all listeners and state
   */
  const init = async () => {
    // Store original title and favicon
    originalTitle.value = document.title
    const link = document.querySelector("link[rel*='icon']")
    if (link) {
      originalFavicon.value = link.href
    }

    // Initialize audio context
    initAudioContext()

    // Request notification permission
    await requestNotificationPermission()

    // Add event listeners
    window.addEventListener('contextmenu', handleContextMenu)
    document.addEventListener('visibilitychange', handleVisibilityChange)
  }

  /**
   * Cleanup
   */
  const cleanup = () => {
    stopTitleBlink()
    stopFaviconBlink()
    window.removeEventListener('contextmenu', handleContextMenu)
    document.removeEventListener('visibilitychange', handleVisibilityChange)
    if (audioContext.value) {
      audioContext.value.close?.()
    }
  }

  // Lifecycle hooks
  onMounted(() => {
    init()
  })

  onBeforeUnmount(() => {
    cleanup()
  })

  return {
    isBlockRightClickEnabled,
    isNotificationsEnabled,
    requestNotificationPermission,
  }
}
